	module.exports = {"data":{"challengeNode":{"title":"Count Backwards With a For Loop","description":["for循环也可以逆向迭代，只要我们定义好合适的条件。","为了能够从后往前两两倒数，我们需要改变我们的<code>初始化</code>，<code>条件判断</code>和<code>计数器</code>。","我们让<code>i = 10</code>，并且当<code>i > 0</code>的时候才继续循环。我们使用<code>i -= 2</code>来让<code>i</code>每次循环递减 2。","<blockquote>var ourArray = [];<br>for (var i=10; i &#62; 0; i-=2) {<br>&nbsp;&nbsp;ourArray.push(i);<br>}</blockquote>","循环结束后，<code>ourArray</code>的值为<code>[10,8,6,4,2]</code>。","让我们改变<code>初始化</code>和<code>计数器</code>，这样我们就可以按照奇数从后往前两两倒着数。","<hr>","使用一个<code>for</code>循环，把 9 到 1 的奇数添加进<code>myArray</code>。"],"challengeType":1,"fields":{"slug":"/javascript-algorithms-and-data-structures/basic-javascript/count-backwards-with-a-for-loop","blockName":"Basic JavaScript","tests":[{"text":"你应该使用<code>for</code>循环","testString":"assert(code.match(/for\\s*\\(/g).length > 1, '你应该使用<code>for</code>循环');"},{"text":"你应该使用数组方法<code>push</code>.","testString":"assert(code.match(/myArray.push/), '你应该使用数组方法<code>push</code>.');"},{"text":"<code>myArray</code>应该等于<code>[9,7,5,3,1]</code>.","testString":"assert.deepEqual(myArray, [9,7,5,3,1], '<code>myArray</code>应该等于<code>[9,7,5,3,1]</code>.');"}]},"required":[],"files":{"indexhtml":null,"indexjs":{"key":"indexjs","ext":"js","name":"index","contents":"// 示例\nvar ourArray = [];\n\nfor (var i = 10; i > 0; i -= 2) {\n  ourArray.push(i);\n}\n\n// 初始化变量\nvar myArray = [];\n\n// 请把你的代码写在这条注释以下\n\n","head":"","tail":"if(typeof myArray !== \"undefined\"){(function(){return myArray;})();}"},"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/javascript-algorithms-and-data-structures/basic-javascript/iterate-through-an-array-with-a-for-loop","id":"56105e7b514f539506016a5e"},"slug":"/javascript-algorithms-and-data-structures/basic-javascript/count-backwards-with-a-for-loop"}}

